.nav {
  text-align: center;
  color: rgba(0, 0, 0, 0.88);;
  font-weight: 600;
  font-family: SegoeUI-SemiBold-final,Segoe UI Semibold,SegoeUI-Regular-final,Segoe UI,"Segoe UI Web (West European)",Segoe,-apple-system,BlinkMacSystemFont,Roboto,Helvetica Neue,Tahoma,Helvetica,Arial,sans-serif;
  font-size: 42px;
  line-height: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 10;
  max-width: 100%;
  background: #ffffff;
  box-shadow: 0 1px 2px 0 rgb(0 0 0 / 3%), 0 1px 6px -1px rgb(0 0 0 / 2%), 0 2px 4px 0 rgb(0 0 0 / 2%);
  img {
    width: 80px;
    height: 80px;
    margin: 10px;
  }
}
footer {
  background: #f0f3fa;
  color: rgba(0, 0, 0, 0.65);
  box-shadow: inset 0 106px 36px -116px rgb(0 0 0 / 14%);
  .footer-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px 0;
    font-size: 16px;
    line-height: 32px;
    text-align: center;
    border-top: 1px solid rgba(255,255,255,.25);
  }
}